import {useAppDispatch, useAppSelector} from '../../hooks';
import {Link, useNavigate} from 'react-router-dom';
import {AppRoute} from '../../const';
import {resetGame} from '../../store/action';
import { logoutAction } from '../../store/api-action';

function WinScreen(): JSX.Element {
  const {mistakes, step} = useAppSelector((state) => state);
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  return (
    <section className="result">
      <div className="result-logout__wrapper">
        <Link onClick={(e) => {
          e.preventDefault();
          dispatch(logoutAction());
        }}  className="result-logout__link" to={AppRoute.Login}
        >
        </Link>
      </div>
      <div className="result__logo"><img src="img/melody-logo.png" alt=" " width="186" height="83"/></div>
      <h2 className="result__title">  !</h2>
      <p className="result__total">    {step - mistakes}    {mistakes} </p>
      <button className="replay" onClick={ () => {
        dispatch(resetGame());
        navigate(AppRoute.Game);
      }} type="button"
      >  
      </button>
    </section>
  );
}

export default WinScreen;

